.BasketList {
  width: 95%;
  display: flex;
  flex-direction: column;
  border-radius: 10px 10px 0 0;
  border: 1px solid gray;
  margin: 15px 0;
}

.BasketList .header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  height: 50px;
  align-items: center;
  background-color: #e7f1ff;
  padding: 10px;
  border-radius: 10px 10px 0 0;
}

.BasketList .header button {
  background-color: #e7f1ff;
  border: none;
  font-size: 16px;
  font-weight: 700;
  transition: background-color 0.1s; /* Optional: Add a smooth transition effect */
}

.BasketList .header button:hover {
  color: rgb(234, 126, 19);
}

.BasketList .header h2 {
  color: #0862e4;
}

.BasketList .list {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.BasketList .list .list-item {
  width: 100%;
  display: flex;
  align-items: center;
  height: 100px;
  margin: 15px 0;
}

.BasketList .list-item img {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  margin: 0 10px;
}

.BasketList .list-item p {
  font-size: 25px;
  font-weight: 300;
  margin: 0 30px;
}
